* {
  padding: 0;
  margin: 0;
}

body {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


/** home page styles **/

.mySpace{
  width: 100vw;
  height: 100vh;
  position: relative;
  background-image: radial-gradient(circle at 10px 10px, indigo , black 70%);
}




.item1{
  position: absolute;
  left: 15%;
  top: 17%;
  justify-content: baseline;
  margin-right: 50vw;
}

.item1 h1{
  font-size: 800%;
  color: red;
}

.item1 .hello{
  color: white;
}



.item2{
  position: absolute;
  align-items: baseline;
  left: 69%;
  top: 35%;
  z-index: 10;
}


.item2 a{
  color: black;
}





.proton {
  width: 200px;
  height: 200px;
  background-color: deepskyblue;
  border-radius: 50%;
  position: relative;
}

.planet {
  width: 100%;
  height: 100%;
  /* background-color: black; */
  position: absolute;
  animation: rotation 100s infinite linear;
  border-radius: 50%;
  padding: 150px;
  border: 1px solid silver;
  left: -74%;
  top: -74%;
  cursor: pointer;
  pointer-events: none;
}

.planet::before {
  display: flex;
  width: 100px;
  height: 100px;
  position: absolute;
  border-radius: 50%;
  font-size: 1.5rem;
  font-weight: bold;
  justify-content: center;
  align-items: center;
  text-align: center;
  animation: planetRotation 100s infinite linear;
  margin: 130px;
  pointer-events: all;
}


.electron::before {
  content: "contact";
  background: radial-gradient(circle at 65% 15%, white 1px,#EADCD7 3%, #D1261D 30%, #006B17 100%);
  left: -100px;
  top: -100px;
}


.neutron::before {
  content: "Projects";
  background: radial-gradient(circle at 65% 15%, #EADCD7 3%, #24D166 30%, #096B56 100%);
  right: -100px;
  top: -100px;
}


.about-me::before {
  content: "About me";
  background: radial-gradient(circle at 65% 15%, white 1px, aqua 3%, rgb(12, 12, 203) 60%, aqua 100%);  
  right: -100px;
  bottom: -100px;
}


.skills::before {
  content: "My Skills";
  background: radial-gradient(circle at 65% 15%,white 1px, #EADCD7 3%, #D19A2A 60%, #FAC04B 100%);
  left: -100px;
  bottom: -100px;
}




@keyframes rotation {
  0% {
    transform:  rotate(-360deg);
  }
}

@keyframes planetRotation {
  0% {
    transform: rotate(360deg);
  }
}

